    review:"(Fall 2020 Online) I gave this a 8/10 in difficulty. This class is HEAVY MEMORIZATION and there is so so so so so so so much information. It's not the professors fault honestly, it's just what this class entails. You also have to be able to build upon information. I took it with Dr. Scott Kelly. He does recorded lectures with set of notes and holds live office hours. The videos can be long and kind of boring because of his monotone voice. I recommend you read the notes before you start the videos, that way you learn and retain more during the videos. There are 2 noncumulative midterms, 3 noncumulative quizzes, and 1 cumulative final. Please take this class seriously and study at least one week before each exam or quiz. Dr. Kelly did a great job of providing \"What you need to know\" slides at the end of each lecture so that you can really pinpoint what to study. There is NO CURVE and NO EC. I also recommend doing well on the very first midterm as that will set the stage and also get 100% on your lab portion as that is 25% of your final grade and can really give you that boost. I somehow scraped by with a B.",
