    review:"The other answer on this explains it pretty well already - Professor Jasso caps the assignments at a B+/A- for some reason. The worst part of the class is that it's at 7:30 AM, so he knows that a lot of people aren't going to attend lecture. Honestly, he's a cool dude and pretty funny at times, but he can be THAT professor that takes attendance when he sees that people aren't showing up. There are weekly reading assignments that you have to write in 10 pt font single space. You get used to it and it really doesn't seem that bad when writing - the reading part is what takes a lot. In the syllabus there's a format for all the readings, so make sure you answer the questions, using the key words of the format. The TAs are going to receive hundreds of 3 pages of 10 pt font single space assignments, they're not going to read it word for word. For the midterms (2 separate days of 2 questions out of 4, so one day one question, the other day another), Jasso is going to choose it by random. He crumbles up papers with the question numbers on them, makes students switch it around, and reveals it in front of the class. With all this entertainment, the midterm and the final (that's during week 10, but choosing 2 out of 5 questions) only allow for 30 minutes of writing time. On the exams, he does say to reference lecture for some of them. It's hard to go to this class with it being so early in the morning, but hopefully you all have the drive to do it. If you do end up not going to class, try your best for the ones before the midterms and finals. He might talk about it. If this is one of the first core classes you take, brace yourself for another Jasso class with the same format (BUS103).",
