    id:313,
    name:"BUS165A",
    rating:"9",
    review:"Had Prof Ke Li (Fall 2018). This is one of those \"weeder\" classes. If you truly want to pursue accounting, this class requires you to attend EVERY SINGLE LECTURE. The book is not required, you just need to purchase an access code for BUS 165 series, this code will be used for the entire series (A-C). Attendance is mandated and points are deducted for every lecture missed. There's a total of 20 points, miss 10 lectures, you receive no participation points when calculating grades. The course is challenging, yes, however, study well and try to achieve the highest possible score possible on each assignment (homework, pre-lecture quizzes, tests, etc.). You will need to keep track of the assignments due after and before each week as he does not remind you, and it's a lot of assignments if you don't keep up. The class is graded based upon the weighted grade or total points achieved, whichever one is higher. This is a class where if you don't put in work, you will fail, even if the professor says \"85% of the class will get a B- or higher\". Exams will have questions from the CPA exam. Overall, if you think accounting upper divs is an easy A, this major/concentration is not for you.",
