    id:428,
    name:"CHEM001B",
    rating:"5.76",
    review:"[COVID-19 SPRING 20] I had this class ONLINE with Linlin Zhao. Attending the Zoom lecture was recommended and he occasionally tossed in some clicker questions to reward those who actively attended. Chem 1B builds off of the information you learned in Chem 1A so it's good to brush up on some of those topics. Like others are saying, this class is a lot of equations and plugging and chugging. The word problems are tricky too. Each chapter was a new concept for me, so it took me a while (and a lot of Khan Academy...) to fully understand the concepts and how the equations worked. Reading the textbook is extremely helpful and he bases most of his exam questions off of what he teaches on the slides. Zhao presents his information pretty thoroughly and takes the time to help students who are stuck. This class moves fairly quickly, so understanding all of the concepts is vital to earning a good grade. Unfortunately, the exams are all closed book and proctored. Clicker questions (20%), discussion points (10%), and homework (20%) make up 50% of the grade (as well as potential bonus points). For us, the homework had unlimited tries and there was no penalty for wrong answers. There are 2 midterms (10% each) and a final (30%). The exams aren't curved (I think), but the final class grade is based on a curve. Overall, I enjoyed this class a lot and I recommend taking it with Zhao!",
