    review:"Your overall grade in this class is determined by your scores in the lab reports and the final assessment. The final assessment is the easiest part of the lab. They will provide you both the questions and answers for the final assessment, so I recommend you practice these questions over the quarter on a regular basis to memorize the answers and score perfect on this category. What determined your final grade came from how well you did overall in the class RELATIVE to your peers in all lab sections. Many people found out the hard way that their \"raw score\" didn't not exactly correlate with their final score. (e.g. 94% raw score is actually a B in the class because Hooley gave out letter grades after \"adding the normalized lab scores (average ~200), the lab exam (average ~230), ranking the class and assigning grades based on that ranking. [...] Your grade is NOT dependent on a raw score, it is dependent on your position in the class.\" So beware of that. :)",
