    review:"CS12 is a relatively difficult class if you don't study the material and actually understand it. Midterms aren't too hard. Had to retake this class even though I had a B overall because I got a 68% on the final(anything lower than a %70 requires you to retake the class). Second time thru I got a solid A. Do your work thats it.",
