    review:"[COVID-19 ERA] This class with a Professor Paea LePendu was a pleasure to partake in. Data Structures and Algorithms is an initially scary class as the jump/learning curve from CS012 was drastic and you had to be really confident in your ability to program. If you don't understand the concepts from the previous class, beware, this class will be difficult programming-wise. Conceptual understanding wasn't a problem, as LePendu really makes sure people nail that portion of the subject. It's not too difficult of a class but taking it online because of COVID-19 did add some learning struggles. The midterm was easy but the final was HARD. I do wish that some of the programming labs that were smaller in size would be 1-person labs as working on them with a group meant you didn't do much coding on your own. More coding practice would've been better as all technical interviews pull from the knowledge you learn in CS014. The most fun CS class so far, though coding some of the challenge activities was mentally stressful lol.",
