    review:"I had the fortune of taking this class with an individual who had already taken this course prior. He pretty much carried me through the quizzes (group quizzes), i think we averaged a 9/10 with lowest dropped. The quiz problems were similar to last quarters problems. Midterm was decently hard, but again similar to a previous quarters midterm. I believe i received a 95% on the midterm. The class as a whole though 18% got an A on the midterm. Final was much tougher. (remember, you need 70% or higher to pass the class). I believe the uncurved average was something like 55%. Lucky for us, he bell curved it to increase the average to 66.6%. I would have failed the class, but luckily ended up with a 72% on the final after the curve. Programming assignments aren't too hard. Labs are killer. Homework is ok, just at least attempt you'll get 100. The midterm and final is the toughest part of the class. It's pretty much a mandatory class for a lot of BCOE majors though, so good luck",
