    review:"Took it Fall 2021, I despised having elena strzheletska as a professor, I don't know why she's still employed at UCR. I was interested and excited to take a class in Data Science so I took this class but her inability to teach and assign course work in a manageable completely RUINED the experience for me. she was extremely rude and unprofessional: she threatened to give quizzes if students didn't show up to her monotonous boring 8 am lectures, verbatim told us she wants to make us \"suffer\", when the majority of the class would show up and a few students would be coughing she would complain about that. she never went over Python examples even once during lecture (most likely because she has no idea how to code), assigned a final project, quiz, and a midterm (which was essentially a final) all during week 10 right before finals. her instructions are so vague and she never provides any clarity on project and quiz requirements and grades students extremely harshly when we have no idea what the guidelines are. she makes mistakes on exam questions often and students have to be the one to correct her after the quiz deadline passed. the course content is not too difficult but please save yourself the stress and take it a different quarter with anyone but her, it's worth the wait. I am not lying when I say she is truly one of the most incompetent professors in the UC Riverside CS department.",
