    review:"First time this class has ever been offered. Took with Professor Mariam and she's super sweet and cares about the students a lot, definitely will listen to any feedback students give her. Course structure: 35% labs, 30% final project, 30% quizzes, and 5% participation. Honestly not a difficult class but super unorganized, which is not a surprise considering it's a new class being taught for the first time. Mariam was super understanding of all of the students and even removed a couple quizzes/labs as the quarter went on, and made the final project grading basis easier. Not a bad class to take if you want to learn the fundamentals of web development but for a class like this where the technology used in industry is constantly changing everyday, it might be better to self-learn it.",
