    id:748,
    name:"CS111",
    rating:"7.77",
    review:"Took with Elena. I was not used to proofs, so it was a rough ride in the beginning. I also had to miss every Friday lecture.\n\nThe tempo of the class was annoying. Each week, I was either stressing over the homework or a quiz. It can get pretty overwhelming at times. Lecture won't really teach you much. She basically just wants you to learn everything on your own. Attendance was taken irregularly during lecture and used as extra credit. Very rarely there would be a bonus problem at the end of lecture for a small extra credit.\n\nYou have enough time and resources for the homework. However, the quizzes might catch you off guard. Around half of the questions will be similar to the provided sample quizzes, but the last questions are going to be related to using proofs from lecture. If given the chance, do the homework before the quizzes. There were a number of times where the homework and quiz questions were near identical.\n\nIn the end, the material was not that bad, but the way it's presented to you can be confusing at times. ",
