    review:"This class is as hard as you hear it is, and definitely the one I spent the most time on this quarter. 5 quizzes, 5 homeworks and ofc a final. The first quiz is basically a CS11 review and is ROUGH for everyone, as she basically expects you study for it by yourself. The only real concept that we use a lot from CS11 is induction and the score for this quiz can get replaced later. \nMake sure to start the homeworks as early as you can, USE HER OFFICE HOURS as they honestly helped me a ton. Do the problems on the quiz syllabuses and the practice quizzes, as they definitely help out. Elena is an alright lecturer, but be sure to pay attention as a lot of the material builds off each other. \nOur final was online cause of COVID-19, open note and on zoom, but pretty concept based. The average of our quiz scores was used to form 1 half off the final grade, pretty sick imo. \n\nDefinitely can't slouch in this class.",
