    review:"I took CS111 Summer 2020 with Elena. We had 3-hour sessions Mondays and Wednesdays mornings - Elena gave us 10 minute break about halfway.  For the summer, Elena gave out quizzes, worth 45% total. Quizzes were open note (no looking at slides). Final was closed-book and completely on graphs. Elena often gives us 5-10 to upload exam material to iLearn - hard if youre uploading pictures.\n\nFinal grade was composed of grade on final exam and grade on quizzes. I highly recommend doing well on quizzes (80% or higher, if possible). Elena allowed us to replace bad scores on the quizzes with practice problems (similar to those we got wrong). After the final, Elena required us to state whether we cheated or not (submit pdf on Gradescope).Elena has a very strong, somewhat annoying, emphasis on cheating - to the point where it's very annoying.\n\nElena also almost always never posts the submission links for homework until you tell her to. For example, if homework is due Wednesday 11:59 pm and you dont see the submission link at Tuesday 8 pm, email her to tell her to release the submission.",
