    name:"CS130",
    rating:"8.11",
    review:"Computer Graphics with Tamar Shinar. The class is definitely difficult. It's a fair mix of programming and concept. There are two major projects, a ray tracer and graphics pipeline that each take about half the quarter to complete. There was one midterm and one final. The average on the exams was around a 70% for our class, which she said was a little on the higher side. 50% is a B and 75% is an A on the exams. To prepare for the exams, make sure to use the practice ones posted on her site. Make sure to utilize the textbook as well. The labs were manageable, and sometimes provided hints toward the projects. It was a difficult class but definitely rewarding and I learned quite a bit.",
