    review:"Ok, so most of the time, you have no idea what you are doing or what is going on. I took this class with Professor Stefano Lonardi. I think he is a good teacher, it's just this is a difficult class. A friend took this class before, dropped out halfway through because he failed his midterm. He retook it with me, and said he prefers Stefano to his old teacher, as he explains clearly and uses lecture notes/slides to his advantage. Do not underestimate this class though. The average on both midterms was a 62%. Stefano does curve, he drops lowest scoring question on both the midterms and the final. The midterms were 4 questions/25 points each, the final was 10 questions/10 points each. The class itself is curved, the A range is 100-80%, B is 70-79.9%, C is 60-69.9%.. etc. So you are given that 10% grace. This is the first 'hard' cs class usually taken by undergraduates, at least for me, and it's a tough one. Do not underestimate it.",
