    review:"Took this class with Ahmed Eldawy. Honestly, this class was pretty difficult for me. In the beginning, you're dealing with Master Theorem, which isn't too hard once you study it and understand it completely. But, once you start touching upon Greedy Algorithms and Dynamic Programming is when the class really starts to feel really difficult. You must study the material as soon as you can; otherwise, you'll feel really left behind, and it will result in you not knowing what the hell you're writing about on your quizzes and/or homework assignments. After Greedy and DP, you deal with shortest path algorithms, which weren't super difficult, but still left an impact on you. Overall, CS141 is probably one of the hardest upper-division CS courses you'll ever take. It's not easy to get an A in this class; you'll be happy with a C, and you'll know exactly why.",
