    review:"Took with Song Spring 2019. Overall I thought this class was moderately difficult at least compared to what other people have said. BUT that's because Song reduced the difficulty of the tests, which he mentioned in the beginning. He gave us all the topics before the tests as well some previous exam questions that ended up on the tests. Homework questions also appeared on the tests. The average for the midterm was a around high 70 percent I think.  Though, maybe this'll change so watch out. Also, and I'm not sure if the other people that reviewed the class had the same lab manual as us, the labs were fairly straightforward. The manuals tell you all the code changes you need to do in the sense that it tells you what functions to change and in what file. Though it can be hard to actually implement the changes you need to do. \nTLDR: Exams and homework fairly decent in terms of difficultly. Labs are the hardest part, but always ask for help from others or TA when you're stuck. If you want to get an in depth knowledge of OS then read the book, its actually a simple and interesting read IMO.",
