    review:"Take Taylor Evans!! He is super nice and genuinely wants to help students succeed. I got a B on my first essay, and I did revisions on it and got an A-. I went to his office hours and my grades on the essays improved. He ended up bumping me up to an A- from a B+ because he thought it was fairer due to my progress in the class. As long as you show him that you are willing to learn and improve your skills, as well as pay attention in class and complete your readings, you will get a good grade. ",
