    review:"Honestly, you're ENGL001B class experience, and your English class experience in general depends on the professor you get. With that said, avoid Rory Moore. She assigns so much work, and doesn't announce quizzes because \"they're on the syllabus\" (17 pages might I add). She reports people to academic misconduct for plagiarism without even informing them. Literally half the people in her class (including myself) dropped because of the heavy workload. Essay prompts were confusing and boring. She claims the workload is the same for all ENGL001B classes, but when I retook the class with Lorelei Baughman, we had exponentially AND significantly way less work compared to Rory Moore. Speaking of Lorelei Baughman, she's an absolute sweetheart, and an amazing professor. At one point she was wondering why we hardly asked questions, and our class had to explain to her that she does an absolute fine job at explaining. Encourages us to reach out to her. if you ever see her class open TAKE IT. Our class focused on food, and she always assigned these fun readings. Our final was easy, essay prompts were easy. She teared up when we told her at the end of the quarter how much we would miss her class.",
