    name:"ENGR180W",
    rating:"3.54",
    review:"Overall, the content of the class is super easy, but the professors make it extremely annoying. \n\nGroup Assignments:\nPRAY that you're assigned quarter-long group mates that are competent and FUCK YOU if you're the group mate that makes everyone else do all the work :(\nBeware the writing guidelines. I can't stress enough, for group assignments make sure all members of your group follow the writing guidelines or everyone in the group loses points. I easily could have gotten an A in the class, but I had to do all of the heavy lifting for 3 other people in my group and couldn't focus on writing my extra credit essays.\n\nEssays:\nLong, tedious, and professors are annoyingly strict on their \"writing guidelines\". Very straight forward though. Set aside like a Saturday morning to bang out an essay and you'll be fine.\n\nExtra Credit:\nDO ALL EXTRA CREDIT. The class offers so many opportunities to make up any points you lost because of dumb mistakes on assignments. It's entirely possible to skip the written part of the final if you do all of the extra credit.",
