    review:"I took Professor Enrique Lopez's Intro to Chicano Studies class in the winter quarter of 2021. There is no structure at all for this class. The lectures are all over the place. There are no slides or lecture outlines, just him ranting about Chicano history and stuff. Also, he requires our camera to be on for attendance purposes, and most of us just woke up. The readings, though short, are shared through blurry jpgs, so they are tedious to read.   His assignments are film review essays. He makes us watch hour-long documentaries and write about it with a very vague prompt.  Honestly, I learned more from those documentaries than I ever did in his class. He also expects us to email him the assignments instead of having us submit them on ilearn. One of my assignments got lost in his spam folder! He takes his sweet-ass time grading assignments.The midterms and finals are just as bad as the assignments, except the TAs are grading them so each TA has their own guidelines. At least they are take-home. The timeline of this class is also very messed up. Our first assignment is assigned on week 5. Our midterm is on week 7, the same day a homework assignment is due.  Our last assignment is due the day of finals, and also can be submitted hours after the final is due.  This class would work much better asynchronous. Do yourself a favor. Do not take this class. Take a different ethnic studies class. Take Asian-American studies or African American Studies or religion. If you decide to be brave and take this class, take it with friends, join group chats, and have a solid support system. You're going to need it.",
