    id:1496,
    name:"HNPG002W",
    rating:"5.5",
    review:"Took this online. Cardullo is the professor who gives the lectures which are very insightful on real world problems and important things about Riverside, so it's more interesting than English classes, but the TA grades everything including essays and participation. Lectures were asynchronous and we had to submit a google form reflection on them which were simple, but discussion with TA met once a week. We had to read 2 books and had discussions on those/lectures each week and also had student led discussions. I struggled in this class because I'm a reserved person and found it hard to participate since a lot of honors students are well-spoken and always have something to say. I also dislike writing essays, so that was a struggle to me too but there were 4 essays of increasing length. I averaged A- on the essays(which was about the class average of each essay) and somehow ended up with an A in the class, so I'm guessing most people get A's in the class or at least half the class does as long as you participate, show up to discussion, and do decent on the essays. If you struggle with writing, they offer lots of help/resources. Don't procrastinate on the essays, so you can get advice/feedback from TAs and other writing helpers. I also never got the score for my participation/last essay, so I'm not sure how they grade those lol, but I didn't participate every time, I only made sure to participate in the student led discussions, but it probably depends how picky your TA is. If you're good with participating in class and don't struggle that much with writing, then this class should not be too bad.",
