    review:"Took with Professor Hong. Should be generally easy like the rest of the intro to language classes. The quizzes, midterm, and final will be testing you on vocabulary and the conjugations that you learn in class - it really just takes practice and are literally taken from lecture. Make sure you know the spelling! There are vowels that sound the same but are spelled differently - the only way to learn the difference is to remember it. The only \"difficult\" thing might be the group project. We have a choice to do a skit in class or through a video, so a majority did the video. As Korean 1, you only learn the very basic grammatical structures, so the video, if a more fun/creative idea (ex: Kdrama parodies), it requires you to speak in sentences we wouldn't have been able to learn. Using Google translate would probably be okay but it'd be obvious and wouldn't sound too great to the professor. The professor is super lenient with the video so just have fun with it. Filming and editing does take a while so I'd recommend to start early. It's a pretty fun class, would recommend it if you're not sure which language to take.",
