    review:"You cover the basics of the whole ME curriculum in this one class. Two midterms, one final. You need to do good on at least one midterm because the final ends up killing everyones grade. There is a class curve at the end, but you need to make sure you make the original cutoff for a passing grade. I had stahovich so the cutoff was a 62% for a C. The majority of the class does fail however. \n\nHomework will take a good 6-8 hours to complete, quizzes are really bad, and you're expected to spend the majority of your time on this class.\n\nStahovich was a good professor.",
