    review:"I took this course (online) with professor Grittersova. It was made up of 2 take-home examinations, an essay, and participation. We had a weekly live session and some asynchronous lectures. It was an interesting course, you learn about a economic models that progressively built on top of each other. It goes from learning about absolute/comparative advantage to contemporary strategies of economic development (and their rationale). The class requires a lot of books/readings, she provides some but you're expected to buy the rest. If you look hard enough, you can find the pdfs online, so I wouldn't recommend buying them. Overall, enriching course. The professor expects a lot from you (she is rigorous), but it's obvious she does this due to enrich our learning experience. I highly recommend the course. You must do the readings to pass the course (at least skim them).",
