    review:"Took it with professor Irepoglu-Carreras. It was a fascinating course, I'd say that half of the course is learning about political theory (e.g. theories of international relations) and the other half is applying them to contemporary issues (e.g. current environmental issues). The course was online: we had live sessions once a week and multiple short asynchronous lectures to watch. She provides all the readings (you don't have to buy any books). Grading was composed by 4 quizzes (1 each two weeks), a take-home midterm, and a take-home final exam (both exams had a multiple choice section and an essay) . All was open book. Overall, it was an excellent course, so I highly recommend it. Also, the professor is extremely organized (e.g. she color-codes questions), so you won't have any issues with not knowing what to expect.",
